      <p>Также в последнее время распространена практика создания медиа-холдингов, которые объединяют несколько газет, телеканалов и сетевых изданий. Однако отзывы журналистов о работе таких холдингов – в основном негативные. Они отметили, что в руководство приходят совершенно далекие от журналистики люди, без опыта, квалификации и профессионализма, но с большим желанием контролировать. Как правило, у директора такой конторы есть несколько заместителей. «Они просто мешают нам работать. Каждый считает, что должен внести в нашу работу что-то свое, в итоге они лезут туда, где ничего не смыслят и тормозят работу. Вместо эффективного бизнес-управления мы получили ненужную громоздкую надстройку по отмыванию денег» - говорят журналисты. Они также обратили внимание на то, что большая часть денег из бюджета уходит на зарплаты раздутого штата директора и его заместителей. </p> 
      
