      <p>Журналисты казахоязычных СМИ отметили, что часто приходится заниматься подготовкой «нечитаемого и несмотрибельного контента». Есть немало запретных тем, цензуры и ограничений. Журнаисты считают, что в рамках госзаказа нужно поддерживать действительно нужные и важные для общества темы.</p>   

