т 60%, у кого-то – 20%. Как правило, в каждом регионе есть одна русскоязычная и одна казахоязычная газета, считающиеся официальными изданиями местных акиматов.")]),_c('p',[_vm._v("Также в последнее время распространена практика создания медиа-холдингов, которые объединяют несколько газет, телеканалов и сетевых изданий. Однако отзывы журналистов о работе таких холдингов – в основном негативные. Они отметили, что в руководство приходят совершенно далекие от журналистики люди, без опыта, квалификации и профессионализма, но с большим желанием контролировать. Как правило, у директора такой конторы есть несколько заместителей. «Они просто мешают нам работать. Каждый считает, что должен внести в нашу работу что-то свое, в итоге они лезут туда, где ничего не смыслят и тормозят работу. Вместо эффективного бизнес-управления мы получили ненужную громоздкую надстройку по отмыванию денег» - говорят журналисты. Они также обратили внимание на то, что большая часть денег из бюджета уходит на зарплаты раздутого штата директора и его заместителей. ")]),_c('p',{staticClass:"qoute"},[_vm._v("«С прошлого года убрали все гонорары. Средняя зарплата – 115 000 тенге. В районных СМИ – 65 000. Раньше платили 6% от стоимости заказных статей, сейчас ничего нет. Все схемы поломаны». ")]),_c('p',[_vm._v("Немало разногласий насчет тематических направлений. Одни журналисты говорили, что госзаказ нужен для позитивного освещения политики региона, и их издания это понимают и стараются делать это максимально естественно. Другие отмечали, что у заказчиков зачастую бывает слишком много «рвения»: «акимат требует освещения «Рухани жангыру», но не понимает, что именно им нужно. Они хотят сплошной позитив, а мы пытаемся объяснить, что так нельзя. Исключительно позитив и восхваление – это ненормально». В то же время журналисты признают, что материалы по госзаказу нередко пишутся «для галочки»: «Журналист пишет ужасно, не работает над текстом и заголовками. Его задача – отчитаться, что материал вышел. Что дальше – никого не интересует».")]),_c('p',[_vm._v("По мнению многих опрошенных, тематические направления должны быть максимально сужены: «Государство имеет право продвигать свои идеи, но делать это без давления и цензуры. И нет смысла платить за новости, потому что СМИ делают их в любом случае. Государство должно платить за такие материалы, как например фильм про ВИЧ Юрия Дудя. Социально важные, востребованные, актуальные. Чтобы был результат. А нашему государству больше интересен процесс…»")]),_c('p',[_vm._v("Кроме того, периодически заказчики не хотят принимать работы, которые им не нравятся или «не в рамках». Как правило, заказчики сами определяют границы того, что можно говорить, а что нет. Большинство журналистов отметило, что имеет место цензура, когда акимат вмешивается в редакционную политику издания, контролирует, указывает, что и как освещать. Сильно развито право «телефонного звонка». ")]),_c('p',[_vm._v("Насчет необходимости системы госзаказа мнения также разделились. Некоторые считают, что это развращающая политика, оправдание глупости и непрофессионализма. Но в то же время журналисты говорят, что если отменить госзаказ, то 80% СМИ закроются, поскольку не готовы конкурировать («не тот уровень»).")]),_c('p',[_vm._v("Что касается конкуренции в этой сфере, респонденты отметили, что в последнее время появилось много фирм-однодневок, которые изображают солидные издания и нередко забирают многомиллионные лоты. В одном издании сообщили, что после обсуждения и жалоб сотрудники акимата стали более четко и грамотно писать техспецификацию.")]),_c('p',[_vm._v("Среди опрошенных были СМИ, которые принципиально не участвуют в конкурсах госзаказа, потому что считают, что это неминуемо приведет к цензуре. Некоторые публикуют платные материалы, которые заказывают им региональные государственные органы.")]),_c('p',[_vm._v("Журналисты казахоязычных СМИ отметили, что часто приходится заниматься подготовкой «нечитаемого и несмотрибельного контента». Есть немало запретных тем, цензуры и ограничений. Журнаисты считают, что в рамках госзаказа нужно поддерживать действительно нужные и важные для общества темы.")]),_c('p',{staticClass:"qoute"},[_vm._v("«Сидят две головы и час болтают – это и выполнение госзаказа. А где качество, где творчество, и в конце концов, где сама журналистика?»")]),_c('p',[_vm._v("Еще одна проблема - госзаказ выигрывают компании из других регионов, которые совсем не знают, что творится в том регионе где объявляется конкурс по госзаказу. «Это просто способ заработка, который не имеет никакого отношения к госинформзаказу».")]),_c('h2',[_c('span',[_vm._v("Что делать?")])]),_c('ul',[_c('li',[_vm._v("– Ограничить список тем и лотов, делая акцент на аналитику, но не на новости.")]),_c('li',[_vm._v("– Полностью исключить все формы цензуры (вмешательство в редакционную политику, право телефонного звонка, прямые указания, как освещать те или иные события). ")]),_c('li',[_vm._v("– Усовершенствовать процедуры распределения госзаказа (сроки, лоты, техспецификация).")])])])}]

